/* ====================================================================================================================
 * FONT-FACE
 * ====================================================================================================================*/
@font-face {
  font-family: 'syne';
  font-weight: 600;
  src: url(./Syne-Bold.woff);
}

@font-face {
  font-family: 'syne';
  font-weight: 900;
  src: url(./Syne-Extra.woff);
}

@font-face {
  font-family: 'syne';
  font-weight: 400;
  src: url(./Syne-Regular.woff);
}

@font-face {
  font-family: 'syne';
  font-style: italic;
  src: url(./Syne-Italic.woff);
}

@font-face {
  font-family: 'syne-mono';
  src: url(./Syne-Mono.woff);
}

@font-face {
  font-family: "Harmond-ItalicCondensed";
  src: url(./HarmondItalicCondensed/font.woff2) format('woff2'), url(./HarmondItalicCondensed/font.woff) format('woff');
}

